var that, userid, phoneNumber, password, pageOptions;
export default {
    data() {
        return {

        }
    },
    onLoad(options) {
        that = this;
        pageOptions = options;
    },
    methods: {
        formSubmit: function(e) { // 提交表单进行登录
            phoneNumber = e.detail.value.phoneNumber;
            password = e.detail.value.password;
            // 正则表达式验证
            let phoneNumberReg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
            let passwordReg = /^(\w){8,16}$/;
            if (phoneNumber == '') {
                uni.showToast({
                    title: '手机号不能为空',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (!phoneNumberReg.test(phoneNumber)) {
                uni.showToast({
                    title: '手机号格式不正确',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (password == '') {
                uni.showToast({
                    title: '密码不能为空',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (!passwordReg.test(password)) {
                uni.showToast({
                    title: '密码长度6~20且只能由字母、数字、下划线组成',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else {
                uni.showLoading();
                uni.request({
                    url: that.apiServer + 'user&m=login',
                    method: 'POST',
                    header: {
                        'content-type': 'application/x-www-form-urlencoded'
                    },
                    data: {
                        phoneNumber,
                        password
                    },
                    success: res => {
                        res = res.data;
                        if (res.status == "error") {
                            uni.hideLoading();
                            uni.showToast({
                                title:res.data,
                                image: '/static/images/inc/icon_warning.png',
                                duration: 2000
                            })
                            return false;
                        } else if (res.status == "ok") {
                            uni.hideLoading();
                            uni.showToast({
                                title:'登录成功',
                                image: '/static/images/inc/icon_success.png',
                            });
                            // 将登录信息保存到本地
                            uni.setStorageSync('USERID' , res.data.userid + '');
                            uni.setStorageSync('PHONENUMBER', res.data.phoneNumber + '');
                            uni.setStorageSync('realName', res.data.realname);
                            // 跳转
                            if (typeof(pageOptions.backtype) == 'undefined') {
                                setTimeout(e => {
                                    uni.redirectTo({url:'../index/index'})
                                },1500)
                            } else {
                                setTimeout(e => {
                                    if(pageOptions.backtype == 1){
                                        uni.redirectTo({url:pageOptions.backpage});
                                    }else{
                                        uni.switchTab({url:pageOptions.backpage});
                                    }
                                },1500);
                            }
                        }
                    },
                });
            }
        }
    }
}
